body {
  font-family: Lato, sans-serif;
}

@media print {
  .no-print {
    display: none;
  }
}

.dropzone {
  cursor: pointer;
}

.box {
  border: 5px solid #003683;
  color: #003683;
  max-width: 60%;
  padding: 30px;
  margin: 30px auto;
  overflow: hidden;
}

a.box {
  text-decoration: none;
}

.pull-left {
  float: left;
}

.help-text {
  color: #003683;
  max-width: 60%;
  padding: 0 30px;
  margin: 30px auto;
}
.page {
  page-break-after: always;
}

button {
  background-color: #003683;
  border: none;
  color: white;
  padding: 20px 50px;
  margin: 20px auto;
  cursor: pointer;
  font-weight: bold;
  display: block;
  font-size: 1rem;
}

h1 {
  font-size: 3rem;
}

.comment {
  font-size: 1.5rem;
  border: 5px solid #003683;
  border-radius: 20px;
  padding: 30px;
  margin: 30px auto;
  min-height: 20rem;
}

#image-reference {
  display: none;
}

.pull-right {
  float: right;
}
